import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { IServerInformation } from './server-information.interface';
import { RxServerInformationService } from './server-information.service';

@Component({
  selector: 'rx-admin-server-information',
  templateUrl: './server-information.component.html'
})
export class ServerInformationAdminComponent implements OnInit {
  busy: Subscription;
  serverInformation: IServerInformation;

  constructor(private rxServerInformationService: RxServerInformationService) {}

  ngOnInit() {
    this.busy = this.rxServerInformationService.getServerInformation().subscribe((serverInformation) => {
      this.serverInformation = serverInformation;
    });
  }
}
